.container{
    width: 1140px;
    padding: 0px 15px;
    margin: auto;
}

.header{
    margin: auto;
    position: fixed;
    width: 100%;
    height: 80px;
    line-height: 60px;
    top: 0;
    left: 0;
    background: hsl(209, 23%, 22%);
    z-index: 11;
}

.header h1 {
    display: flex;
    align-items: center;
    font-size: 1.3rem;
    color: #fff;
}