.wrapper{
    margin-left: 250px;
}

.country_top{
    display:flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
}

.country_bottom{
    display: flex;
    flex-wrap: wrap;
    column-gap: 2rem;
    row-gap: 2rem;
}

.country_card{
    width: 250px;
    border-radius: 5px;
    background: hsl(209, 23%, 22%);
    color: white;
}

.country_img img{
    width: 100%;
    border-radius: 5px 5px 0px 0px;
}

.all_country_wrapper{
   margin-top: 100px; 
}

.country_details{
    padding: 20px;
}

.country_details h3{
    margin-top: 10px;
    margin-bottom: 15px;
    font-weight: 500;
}

.country_details h6{
    margin-bottom: 10px;
    font-weight: 400;
    opacity: 80%;
}

.country_bottom h4 {
    color: white;
    text-align: center;
    margin-top: 15px;
}



