.country_info_wrapper{
    margin-top: 120px;
}

.country_info_container{
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-top: 50px;
    
}

.country_info_img,
.country_info{
    width:50%;
}

.button{
    padding: 10px 25px;
    
    background: hsl(209, 23%, 22%);
    border: none;
    outline: none;
    cursor: pointer;
    width: 120px;
    border-radius: 5px;
    margin-bottom: 50px;
    margin-left: 30px;
}

a{
    text-decoration: none;
    color: white;
}

.country_info h3{
    color: white;
    font-size: 1.3rem;
    margin-bottom: 30px;
}

.country_info_left h5{
    color: white;
    font-size: 0.9rem;
    font-weight: 400;
    margin-bottom: 10px;
}
.country_info_left span{
    color: white;
    opacity: 85%;
    font-weight: 400;
}

 h4{
    color: white;
}